@use "../utils/extends";
@use "../utils/variables" as *;

.nav__list {
  @extend %resetMarginPadding ;
  display: flex;
  list-style: none;
  flex-wrap: wrap;
  align-items: center;

  &-item {
    list-style: none;
    position: relative;
    text-transform: uppercase;
    text-decoration: none;

    &:not(:last-child) {
      margin-right: 20px;
    }
  }
  &-tag {
    display: inline-block;
    margin: 0;
    text-decoration: none;
    font-size: calc($Smallest-font10 * 1.2);
    font-weight: 500;
    padding: 23px 0;
    color: $main-text-colour;

    &:hover {
      color: $button-on-colour;
    }
  }
}

.is-active {
  color: $button-on-colour;

  &:after {
    position: absolute;
    content: "";
    height: 4px;
    bottom: 0;
    background: $button-on-colour;
    left: 0;
    right: 0;
    border-radius: 8px;
  }
}
